.container {
    display: flex;
}

.button {
    width: 70px;
    border-radius: 2px;
    border: 1px solid;
    border-color: mediumblue;
    background-color: lightblue;
}

.button:hover {
    background-color: indigo;
    color: white;
}

.button+.button {
    margin-left: 30px;
}